<template>
  <div class="admin-records-container">
    <div class="header">
      <h1> - </h1>
    </div>

    <div class="content">
      <div class="actions">
        <el-button type="primary" @click="addNewRecord">
          <el-icon><plus /></el-icon> 
        </el-button>
        <el-button type="success" @click="goToUserManagement">
          <el-icon><user /></el-icon> 
        </el-button>
        <el-button @click="refreshRecords">
          <el-icon><refresh /></el-icon> 
        </el-button>
      </div>
      
      <!--  -->
      <el-card class="filter-card">
        <div class="filter-header">
          <h3></h3>
          <el-button type="primary" link @click="showFilterPanel = !showFilterPanel">
            {{ showFilterPanel ? '' : '' }} 
            <el-icon v-if="!showFilterPanel"><arrow-down /></el-icon>
            <el-icon v-else><arrow-up /></el-icon>
          </el-button>
        </div>
        
        <div v-show="showFilterPanel" class="filter-form-container">
          <el-form :model="filterForm" label-width="100px" class="filter-form">
            <el-row :gutter="20">
              <!--  -->
              <el-col :span="12">
                <el-form-item label="">
                  <el-select v-model="filterForm.unitId" placeholder="" style="width: 100%" clearable>
                    <el-option 
                      v-for="unit in units" 
                      :key="unit.id" 
                      :label="unit.name" 
                      :value="unit.id" 
                    />
                  </el-select>
                </el-form-item>
              </el-col>
              
              <!--  -->
              <el-col :span="12">
                <el-form-item label="">
                  <el-date-picker
                    v-model="filterForm.dateRange"
                    type="daterange"
                    range-separator=""
                    start-placeholder=""
                    end-placeholder=""
                    format="YYYY-MM-DD"
                    value-format="YYYY-MM-DD"
                    style="width: 100%"
                  />
                </el-form-item>
              </el-col>
            </el-row>
            
            <el-row :gutter="20">
              <!--  -->
              <el-col :span="8">
                <el-form-item label="">
                  <el-select v-model="filterForm.wasteTypeId" placeholder="" style="width: 100%" clearable>
                    <el-option 
                      v-for="type in wasteTypes" 
                      :key="type.id" 
                      :label="type.name" 
                      :value="type.id" 
                    />
                  </el-select>
                </el-form-item>
              </el-col>
              
              <!--  -->
              <el-col :span="8">
                <el-form-item label="()">
                  <div class="quantity-range">
                    <el-input-number 
                      v-model="filterForm.minQuantity" 
                      :min="0"
                      :precision="3"
                      :step="0.001"
                      placeholder=""
                      style="width: 47%"
                    />
                    <span class="range-separator"></span>
                    <el-input-number 
                      v-model="filterForm.maxQuantity" 
                      :min="filterForm.minQuantity || 0"
                      :precision="3"
                      :step="0.001"
                      placeholder=""
                      style="width: 47%"
                    />
                  </div>
                </el-form-item>
              </el-col>
              
              <!--  -->
              <el-col :span="8">
                <el-form-item label="">
                  <el-input 
                    v-model="filterForm.location" 
                    placeholder="" 
                    clearable
                  />
                </el-form-item>
              </el-col>
            </el-row>
            
            <div class="filter-actions">
              <el-button type="primary" @click="applyFilter"></el-button>
              <el-button @click="resetFilter"></el-button>
            </div>
          </el-form>
        </div>
      </el-card>
      
      <div class="records-wrapper">
        <el-card class="records-card">
          <div class="card-header">
            <h3></h3>
            <div class="card-actions">
              <el-button type="warning" @click="exportRecords">
                <el-icon><download /></el-icon> 
              </el-button>
            </div>
          </div>
          
          <el-table 
            :data="filteredRecords" 
            style="width: 100%" 
            border 
            v-loading="loading"
            stripe
            class="responsive-table"
            height="500"
            @scroll="handleScroll"
          >
            <el-table-column prop="unit_name" label="" width="100" />
            <el-table-column prop="waste_type_name" label="" width="100" />
            <el-table-column prop="location" label="" />
            <el-table-column prop="collection_start_time" label="" width="160" />
            <el-table-column label="()" width="80">
              <template #default="scope">
                {{ parseFloat(scope.row.quantity).toFixed(3) }}
              </template>
            </el-table-column>
            <el-table-column prop="creator_name" label="" width="100" />
            <el-table-column prop="created_at" label="" width="160" />
            <el-table-column
              label=""
              width="120"
              align="center"
            >
              <template #default="scope">
                <div v-if="scope.row.photo_path_before" class="photo-preview">
                  <!--  -->
                  <div 
                    v-for="(path, index) in parsePhotoPath(scope.row.photo_path_before)" 
                    :key="index"
                    class="photo-thumbnail-container"
                    @click="previewPhoto(parsePhotoPath(scope.row.photo_path_before), index)"
                  >
                    <el-image
                      style="width: 50px; height: 50px"
                      :src="`${baseUrl}${path}`"
                      fit="cover"
                    ></el-image>
                  </div>
                  <div v-if="parsePhotoPath(scope.row.photo_path_before).length > 1" class="photo-count">
                    {{ parsePhotoPath(scope.row.photo_path_before).length }}
                  </div>
                </div>
                <span v-else></span>
              </template>
            </el-table-column>
            <el-table-column
              label=""
              width="120"
              align="center"
            >
              <template #default="scope">
                <div v-if="scope.row.photo_path_after" class="photo-preview">
                  <!--  -->
                  <div 
                    v-for="(path, index) in parsePhotoPath(scope.row.photo_path_after)" 
                    :key="index"
                    class="photo-thumbnail-container"
                    @click="previewPhoto(parsePhotoPath(scope.row.photo_path_after), index)"
                  >
                    <el-image
                      style="width: 50px; height: 50px"
                      :src="`${baseUrl}${path}`"
                      fit="cover"
                    ></el-image>
                  </div>
                  <div v-if="parsePhotoPath(scope.row.photo_path_after).length > 1" class="photo-count">
                    {{ parsePhotoPath(scope.row.photo_path_after).length }}
                  </div>
                </div>
                <span v-else></span>
              </template>
            </el-table-column>
            <el-table-column label="" width="70" fixed="right">
              <template #default="scope">
                <div class="operation-buttons">
                  <el-button 
                    type="primary" 
                    size="small" 
                    @click="editRecord(scope.row.id)"
                    text
                  >
                    
                  </el-button>
                  <el-button 
                    type="danger" 
                    size="small" 
                    @click="confirmDelete(scope.row)"
                    text
                  >
                    
                  </el-button>
                </div>
              </template>
            </el-table-column>
          </el-table>
          
          <div v-if="loadingMore" class="loading-more">
            <el-icon class="loading"><loading /></el-icon>
            ...
          </div>
          
          <div class="empty-block" v-if="records.length === 0 && !loading">
            <el-empty description="" />
          </div>
        </el-card>
      </div>
    </div>

    <div class="footer">
      <p>&copy; 2025 </p>
    </div>

    <!--  -->
    <el-image-viewer
      v-if="showViewer"
      :url-list="previewImages"
      :initial-index="previewIndex"
      @close="closeViewer"
    />
  </div>
</template>

<script>
import { ref, onMounted, computed, reactive } from 'vue';
import { useRouter } from 'vue-router';
import { ElMessage, ElMessageBox, ElImageViewer } from 'element-plus';
import axios from 'axios';
import { Plus, Refresh, User, ArrowDown, ArrowUp, Download, Loading } from '@element-plus/icons-vue';
import auth from '../store/auth';
import { exportToExcel } from '../utils/exportUtils';
import apiConfig from '../config/api';

// APIURL
// const apiBaseURL = apiConfig.baseURL;

// 
const parsePhotoPath = (path) => {
  if (!path) return [];
  
  try {
    // JSON
    if (path.startsWith('[') && path.endsWith(']')) {
      return JSON.parse(path);
    }
    // JSON
    return [path];
  } catch (error) {
    console.error(':', error);
    // 
    return [path];
  }
};

// 
const getPhotosByType = (record, type) => {
  if (!record.photos) return [];
  
  try {
    const photos = JSON.parse(record.photos);
    if (Array.isArray(photos)) {
      return photos.filter(photo => photo.type === type);
    }
    return [];
  } catch (error) {
    console.error('JSON:', error);
    return [];
  }
};

export default {
  name: 'AdminRecordsView',
  components: {
    Plus,
    Refresh,
    User,
    ArrowDown,
    ArrowUp,
    Download,
    ElImageViewer,
    Loading
  },
  setup() {
    const router = useRouter();
    const records = ref([]);
    const loading = ref(false);
    const units = ref([]);
    const wasteTypes = ref([]);
    const showFilterPanel = ref(true);
    
    // baseUrl
    const baseUrl = apiConfig.baseURL;
    
    // 
    const showViewer = ref(false);
    const previewImages = ref([]);
    const previewIndex = ref(0);
    
    // 
    const page = ref(1);
    const pageSize = ref(20);
    const hasMore = ref(true);
    const loadingMore = ref(false);
    
    // 
    const filterForm = reactive({
      unitId: null,
      dateRange: null,
      wasteTypeId: null,
      minQuantity: null,
      maxQuantity: null,
      location: ''
    });
    
    // 
    const filteredRecords = computed(() => {
      return records.value.filter(record => {
        // 
        if (filterForm.unitId && record.unit_id !== filterForm.unitId) {
          return false;
        }
        
        // 
        if (filterForm.wasteTypeId && record.waste_type_id !== filterForm.wasteTypeId) {
          return false;
        }
        
        // 
        if (filterForm.minQuantity !== null && parseFloat(record.quantity) < filterForm.minQuantity) {
          return false;
        }
        if (filterForm.maxQuantity !== null && parseFloat(record.quantity) > filterForm.maxQuantity) {
          return false;
        }
        
        // 
        if (filterForm.location && !record.location.toLowerCase().includes(filterForm.location.toLowerCase())) {
          return false;
        }
        
        // 
        if (filterForm.dateRange && filterForm.dateRange.length === 2) {
          const startDate = new Date(filterForm.dateRange[0]);
          // 23:59:59
          const endDate = new Date(filterForm.dateRange[1]);
          endDate.setHours(23, 59, 59, 999);
          
          // 
          if (record.collection_start_time) {
            // 
            const recordDate = parseFormattedDateTime(record.collection_start_time);
            if (recordDate < startDate || recordDate > endDate) {
              return false;
            }
          } else {
            // 
            return false;
          }
        }
        
        return true;
      });
    });
    
    // 
    const parseFormattedDateTime = (formattedDateTime) => {
      //  "2025-03-01 14:30:00"
      // 
      const cleanedStr = formattedDateTime.replace(/[^0-9\-: ]/g, '');
      return new Date(cleanedStr);
    };

    onMounted(async () => {
      // 
      if (!auth.state.isLoggedIn || auth.state.user.role_id !== 3) {
        ElMessage.error('');
        router.push('/login');
        return;
      }
      
      await Promise.all([
        fetchUnits(),
        fetchWasteTypes(),
        fetchRecords()
      ]);
    });
    
    // 
    const fetchUnits = async () => {
      try {
        const response = await axios.get(apiConfig.getUrl(apiConfig.endpoints.units));
        units.value = response.data;
      } catch (error) {
        console.error(':', error);
        ElMessage.error('');
      }
    };
    
    // 
    const fetchWasteTypes = async () => {
      try {
        const response = await axios.get(apiConfig.getUrl(apiConfig.endpoints.wasteTypes));
        wasteTypes.value = response.data;
      } catch (error) {
        console.error(':', error);
        ElMessage.error('');
      }
    };

    const fetchRecords = async (isLoadMore = false) => {
      if (!isLoadMore) {
        loading.value = true;
        page.value = 1;
        records.value = [];
      } else {
        loadingMore.value = true;
      }
      
      try {
        const params = {
          page: page.value,
          pageSize: pageSize.value,
          wasteTypeId: filterForm.wasteTypeId,
          minQuantity: filterForm.minQuantity,
          maxQuantity: filterForm.maxQuantity,
          location: filterForm.location,
        };
        
        // Only add dateRange if it exists and has both start and end dates
        if (filterForm.dateRange && filterForm.dateRange.length === 2) {
          params.dateRange = JSON.stringify(filterForm.dateRange);
        }
        
        const response = await axios.get(
          `${apiConfig.getUrl(apiConfig.endpoints.wasteRecords)}/user/${auth.state.user.id}`,
          { params }
        );
        
        // 
        const formattedRecords = response.data.records.map(record => ({
          ...record,
          created_at: formatDateTime(record.created_at),
          collection_start_time: formatDateTime(record.collection_start_time)
        }));
        
        if (isLoadMore) {
          records.value = [...records.value, ...formattedRecords];
        } else {
          records.value = formattedRecords;
        }
        
        hasMore.value = response.data.hasMore;
        if (hasMore.value) {
          page.value++;
        }
      } catch (error) {
        console.error(':', error);
        ElMessage.error('');
      } finally {
        loading.value = false;
        loadingMore.value = false;
      }
    };

    const formatDateTime = (dateTimeStr) => {
      if (!dateTimeStr) return '';
      const date = new Date(dateTimeStr);
      return date.toLocaleString('zh-CN', {
        year: 'numeric',
        month: '2-digit',
        day: '2-digit',
        hour: '2-digit',
        minute: '2-digit',
        hour12: false
      });
    };

    const refreshRecords = () => {
      fetchRecords();
    };

    const addNewRecord = () => {
      if (auth.state.user.role_id === 3) {
        router.push('/record/new');
      } else {
        router.push(`/unit/${auth.state.user.unit_id}`);
      }
    };

    const goToUserManagement = () => {
      router.push('/user-management');
    };

    const editRecord = (recordId) => {
      router.push(`/record/${recordId}`);
    };

    // 
    const applyFilter = async () => {
      await fetchRecords();
      ElMessage.success('');
    };
    
    // 
    const resetFilter = async () => {
      filterForm.unitId = null;
      filterForm.dateRange = null;
      filterForm.wasteTypeId = null;
      filterForm.minQuantity = null;
      filterForm.maxQuantity = null;
      filterForm.location = '';
      await fetchRecords();
      ElMessage.info('');
    };
    
    // Excel
    const exportRecords = async () => {
      if (filteredRecords.value.length === 0) {
        ElMessage.warning('');
        return;
      }
      
      try {
        loading.value = true;
        // 
        const response = await axios.get(
          `${apiConfig.getUrl(apiConfig.endpoints.wasteRecords)}/export/user/${auth.state.user.id}`,
          {
            params: {
              unitId: filterForm.unitId,
              wasteTypeId: filterForm.wasteTypeId,
              minQuantity: filterForm.minQuantity,
              maxQuantity: filterForm.maxQuantity,
              location: filterForm.location,
              dateRange: filterForm.dateRange
            }
          }
        );
        
        const allRecords = response.data.map(record => ({
          ...record,
          created_at: formatDateTime(record.created_at),
          collection_start_time: formatDateTime(record.collection_start_time)
        }));
        
        // 
        const exportHeaders = [
          { title: '', field: 'unit_name', width: 15 },
          { title: '', field: 'waste_type_name', width: 15 },
          { title: '', field: 'location', width: 20 },
          { title: '', field: 'collection_start_time', width: 20, type: 'datetime' },
          { title: '()', field: 'quantity', width: 12, type: 'number' },
          { title: '', field: 'created_at', width: 20, type: 'datetime' }
        ];
        
        let fileName = '';
        if (filterForm.unitId) {
          const unit = units.value.find(u => u.id === filterForm.unitId);
          if (unit) {
            fileName = `${unit.name}_`;
          }
        }
        if (filterForm.wasteTypeId) {
          const wasteType = wasteTypes.value.find(type => type.id === filterForm.wasteTypeId);
          if (wasteType) {
            fileName += `_${wasteType.name}`;
          }
        }
        
        exportToExcel(allRecords, fileName, exportHeaders);
        ElMessage.success('');
      } catch (error) {
        console.error(':', error);
        ElMessage.error('');
      } finally {
        loading.value = false;
      }
    };
    
    // 
    const confirmDelete = (record) => {
      ElMessageBox.confirm(
        '',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning',
        }
      )
        .then(async () => {
          try {
            await axios.delete(`${apiConfig.getUrl(apiConfig.endpoints.wasteRecords)}/${record.id}`);
            ElMessage.success('');
            await fetchRecords();
          } catch (error) {
            console.error(':', error);
            ElMessage.error('');
          }
        })
        .catch(() => {
          ElMessage.info('');
        });
    };

    // 
    const previewPhoto = (paths, index) => {
      // paths
      const photoArray = Array.isArray(paths) ? paths : [paths];
      previewImages.value = photoArray.map(path => `${baseUrl}${path}`);
      previewIndex.value = index || 0;
      showViewer.value = true;
    };
    
    // 
    const closeViewer = () => {
      showViewer.value = false;
    };

    // 
    const handleScroll = async (e) => {
      const element = e.target;
      // element
      if (
        element && 
        element.scrollHeight && 
        !loadingMore.value &&
        hasMore.value &&
        element.scrollHeight - element.scrollTop - element.clientHeight < 100
      ) {
        await fetchRecords(true);
      }
    };

    return {
      records,
      filteredRecords,
      loading,
      units,
      wasteTypes,
      showFilterPanel,
      filterForm,
      applyFilter,
      resetFilter,
      exportRecords,
      parsePhotoPath,
      getPhotosByType,
      refreshRecords,
      addNewRecord,
      goToUserManagement,
      editRecord,
      confirmDelete,
      // 
      showViewer,
      previewImages,
      previewIndex,
      previewPhoto,
      closeViewer,
      baseUrl,
      page,
      pageSize,
      hasMore,
      loadingMore,
      handleScroll
    };
  }
};
</script>

<style scoped>
.admin-records-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.header {
  background-color: #409EFF;
  color: white;
  padding: 20px;
  text-align: center;
}

.content {
  flex: 1;
  padding: 30px;
  max-width: 1200px;
  margin: 0 auto;
  width: 100%;
  box-sizing: border-box;
}

.actions {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 20px;
  gap: 10px;
}

.filter-card {
  margin-bottom: 20px;
}

.filter-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
}

.filter-header h3 {
  margin: 0;
  color: #333;
}

.filter-form-container {
  transition: all 0.3s ease;
}

.quantity-range {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.range-separator {
  margin: 0 5px;
}

.filter-actions {
  display: flex;
  justify-content: center;
  margin-top: 20px;
  gap: 10px;
}

.records-wrapper {
  margin-top: 20px;
}

.records-card {
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
}

.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.card-header h3 {
  margin: 0;
  color: #333;
}

.record-image {
  width: 50px;
  height: 50px;
  border-radius: 4px;
  cursor: pointer;
  display: block;
}

.photo-count {
  font-size: 12px;
  color: #409EFF;
  text-align: center;
  margin-top: 2px;
}

.image-error {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
  background-color: #f5f7fa;
  color: #909399;
}

.empty-block {
  margin: 20px 0;
}

.footer {
  background-color: #f5f5f5;
  padding: 15px;
  text-align: center;
  color: #666;
}

.photo-thumbnail-container {
  cursor: pointer;
  margin-bottom: 2px;
}

.photo-count {
  font-size: 12px;
  color: #909399;
  text-align: center;
  margin-top: 2px;
}

.photo-preview {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.operation-buttons {
  display: flex;
  flex-direction: column;
  gap: 4px;
  align-items: center;
}

.operation-buttons .el-button {
  margin-left: 0;
  padding: 4px 8px;
}

.loading-more {
  text-align: center;
  padding: 10px 0;
  color: #909399;
}

.loading-more .loading {
  margin-right: 5px;
  animation: rotating 2s linear infinite;
}

@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
</style>

<style>
/* Element Plusz-index */
.el-image-viewer__wrapper {
  z-index: 2147483647 !important; /* z-index */
  position: fixed !important;
}

/*  */
.el-image-viewer__mask {
  z-index: 2147483646 !important;
  position: fixed !important;
}

/*  */
.el-image-viewer__btn {
  z-index: 2147483647 !important;
  position: fixed !important;
}

/*  */
.el-image-viewer__close {
  z-index: 2147483647 !important;
  position: fixed !important;
}

/*  */
.el-image-viewer__img {
  z-index: 2147483646 !important;
  position: relative !important;
}

/*  */
.el-image-viewer__actions {
  z-index: 2147483647 !important;
  position: fixed !important;
}

/*  */
.el-image-viewer__actions__inner {
  z-index: 2147483647 !important;
  position: relative !important;
}
</style>
