import axios from 'axios';
import { ElMessage } from 'element-plus';
import auth from '../store/auth';
import apiConfig from './api';
import router from '../router';

//  axios 
const axiosInstance = axios.create({
  baseURL: apiConfig.baseURL,
  timeout: 300000, // 300
  headers: {
    'Content-Type': 'application/json'
  },
  maxContentLength: 50 * 1024 * 1024, // 50MB
  maxBodyLength: 50 * 1024 * 1024 // 50MB
});

// 
axiosInstance.interceptors.request.use(
  config => {
    //  localStorage  sessionStorage  token
    const token = localStorage.getItem('token') || sessionStorage.getItem('token');
    if (token) {
      config.headers.Authorization = `Bearer ${token}`;
    }
    return config;
  },
  error => {
    console.error('Request error:', error);
    return Promise.reject(error);
  }
);

// 
axiosInstance.interceptors.response.use(
  response => {
    return response;
  },
  error => {
    if (error.response) {
      switch (error.response.status) {
        case 401:
          // 
          if (error.config.url.includes('/api/login')) {
            // 
            return Promise.reject(error);
          } else {
            // token 
            auth.logout();
            ElMessage.error('');
            router.push('/login');
          }
          break;
        case 403:
          ElMessage.error('');
          break;
        case 404:
          ElMessage.error('');
          break;
        case 500:
          ElMessage.error('');
          break;
        default:
          ElMessage.error(error.response.data?.error || '');
      }
    } else if (error.request) {
      ElMessage.error('');
    } else {
      ElMessage.error('');
    }
    return Promise.reject(error);
  }
);

// HTTP
const httpService = {
  // GET
  get(endpoint, params = {}) {
    return axiosInstance.get(endpoint, { params });
  },
  
  // POST
  post(endpoint, data = {}) {
    return axiosInstance.post(endpoint, data);
  },
  
  // POST
  postForm(endpoint, formData, onUploadProgress) {
    const config = {
      headers: {
        'Content-Type': 'multipart/form-data'
      },
      timeout: 300000, // 300
      maxContentLength: 50 * 1024 * 1024, // 50MB
      maxBodyLength: 50 * 1024 * 1024 // 50MB
    };
    
    // onUploadProgress
    if (typeof onUploadProgress === 'function') {
      config.onUploadProgress = onUploadProgress;
    }
    
    return axiosInstance.post(endpoint, formData, config);
  },
  
  // PUT
  put(endpoint, data = {}) {
    return axiosInstance.put(endpoint, data);
  },
  
  // PUT
  putForm(endpoint, formData, onUploadProgress) {
    const config = {
      headers: {
        'Content-Type': 'multipart/form-data'
      },
      timeout: 300000, // 300
      maxContentLength: 50 * 1024 * 1024, // 50MB
      maxBodyLength: 50 * 1024 * 1024 // 50MB
    };
    
    // onUploadProgress
    if (typeof onUploadProgress === 'function') {
      config.onUploadProgress = onUploadProgress;
    }
    
    return axiosInstance.put(endpoint, formData, config);
  },
  
  // DELETE
  delete(endpoint) {
    return axiosInstance.delete(endpoint);
  },

  //  axios  defaults
  defaults: axiosInstance.defaults
};

export default httpService;
