import { reactive } from 'vue';
import httpService from '../config/httpService';
import apiConfig from '../config/api';

// 
const defaultState = {
  user: null,
  token: null,
  isLoggedIn: false,
  loading: false,
  error: null
};

// 
const state = reactive({
  ...defaultState
});

// localStoragetoken
const init = () => {
  const savedUser = localStorage.getItem('user');
  const savedToken = localStorage.getItem('token');
  if (savedUser && savedToken) {
    try {
      const user = JSON.parse(savedUser);
      state.user = user;
      state.token = savedToken;
      state.isLoggedIn = true;
      //  token  httpService
      if (httpService.defaults && httpService.defaults.headers) {
        httpService.defaults.headers.common['Authorization'] = `Bearer ${savedToken}`;
      }
    } catch (e) {
      console.error('Error parsing saved user data:', e);
      localStorage.removeItem('user');
      localStorage.removeItem('token');
    }
  }
};

// 
const login = async (phone, password, rememberMe = false, userType = 1) => {
  // 
  state.loading = true;
  state.error = null;
  
  console.log('auth.login :', phone, ':', userType);

  try {
    // 
    const postData = { 
      phone, 
      rememberMe,
      userType // 
    };
    
    // 
    if (password !== null && password !== undefined && password !== '') {
      postData.password = password;
      console.log('auth.login ');
    } else {
      console.log('auth.login ');
    }
    
    console.log(':', postData);
    const response = await httpService.post(apiConfig.endpoints.login, postData);
    console.log(':', response.data);

    const { token, ...user } = response.data;
    state.user = user;
    state.token = token;
    state.isLoggedIn = true;
    
    //  token  httpService
    if (httpService.defaults && httpService.defaults.headers) {
      httpService.defaults.headers.common['Authorization'] = `Bearer ${token}`;
    }
    
    // localStorage
    if (rememberMe) {
      localStorage.setItem('user', JSON.stringify(user));
      localStorage.setItem('token', token);
    } else {
      //  sessionStorage 
      sessionStorage.setItem('user', JSON.stringify(user));
      sessionStorage.setItem('token', token);
    }
    
    return { success: true, user };
  } catch (error) {
    // 
    state.error = error.response?.data?.error || '';
    console.error('Login error:', error);
    return { success: false, error: state.error };
  } finally {
    state.loading = false;
  }
};

// 
const logout = () => {
  state.user = null;
  state.token = null;
  state.isLoggedIn = false;
  if (httpService.defaults && httpService.defaults.headers) {
    delete httpService.defaults.headers.common['Authorization'];
  }
  localStorage.removeItem('user');
  localStorage.removeItem('token');
  sessionStorage.removeItem('user');
  sessionStorage.removeItem('token');
};

// 
const isAdmin = () => {
  return state.isLoggedIn && state.user && state.user.role_id === 3;
};

// 
const isUnitAdmin = () => {
  return state.isLoggedIn && state.user && state.user.role_id === 2;
};

// ID
const getUserId = () => {
  return state.user ? state.user.id : null;
};

// ID
const getUnitId = () => {
  return state.user ? state.user.unit_id : null;
};

// 
init();

export default {
  state,
  login,
  logout,
  isAdmin,
  isUnitAdmin,
  getUserId,
  getUnitId
};
