<template>
  <div class="records-container">
    <div class="header">
      <div class="back-button" @click="goBack">
        <el-icon><arrow-left /></el-icon> 
      </div>
      <h1></h1>
      <div class="home-link" @click="goHome" v-if="isAdmin">
         <el-icon><home-filled /></el-icon>
      </div>
      <div v-else></div>
    </div>

    <div class="content">
      <div class="unit-info">
        <h2>{{ unitName }} - </h2>
      </div>
      
      <div class="actions">
        <el-button type="primary" @click="addNewRecord">
          <el-icon><plus /></el-icon> 
        </el-button>
        <el-button type="success" @click="goToUserManagement" v-if="isAdmin || isUnitAdmin">
          <el-icon><user /></el-icon> 
        </el-button>
        <el-button @click="refreshRecords">
          <el-icon><refresh /></el-icon> 
        </el-button>
        <el-button type="primary" @click="exportRecords" :loading="loading">
          <el-icon><download /></el-icon> 
        </el-button>
      </div>
      
      <!--  -->
      <el-card class="filter-card">
        <div class="filter-header">
          <h3></h3>
          <el-button type="primary" link @click="showFilterPanel = !showFilterPanel">
            {{ showFilterPanel ? '' : '' }} 
            <el-icon v-if="!showFilterPanel"><arrow-down /></el-icon>
            <el-icon v-else><arrow-up /></el-icon>
          </el-button>
        </div>
        
        <div v-show="showFilterPanel" class="filter-form-container">
          <el-form :model="filterForm" label-width="100px" class="filter-form">
            <el-row :gutter="20">
              <!--  -->
              <el-col :xs="24" :sm="24" :md="12" :lg="12" :xl="12">
                <el-form-item label="">
                  <el-date-picker
                    v-model="filterForm.dateRange"
                    type="daterange"
                    range-separator=""
                    start-placeholder=""
                    end-placeholder=""
                    format="YYYY-MM-DD"
                    value-format="YYYY-MM-DD"
                    style="width: 100%"
                  />
                </el-form-item>
              </el-col>
              
              <!--  -->
              <el-col :xs="24" :sm="24" :md="12" :lg="12" :xl="12">
                <el-form-item label="">
                  <el-select v-model="filterForm.wasteTypeId" placeholder="" style="width: 100%" clearable>
                    <el-option 
                      v-for="type in wasteTypes" 
                      :key="type.id" 
                      :label="type.name" 
                      :value="type.id" 
                    />
                  </el-select>
                </el-form-item>
              </el-col>
            </el-row>
            
            <el-row :gutter="20">
              <!--  -->
              <el-col :xs="24" :sm="24" :md="12" :lg="12" :xl="12">
                <el-form-item label="()">
                  <div class="quantity-range">
                    <el-input-number 
                      v-model="filterForm.minQuantity" 
                      :min="0"
                      :precision="3"
                      :step="0.001"
                      placeholder=""
                      style="width: 48%"
                    />
                    <span class="range-separator"></span>
                    <el-input-number 
                      v-model="filterForm.maxQuantity" 
                      :min="filterForm.minQuantity || 0"
                      :precision="3"
                      :step="0.001"
                      placeholder=""
                      style="width: 48%"
                    />
                  </div>
                </el-form-item>
              </el-col>
              
              <!--  -->
              <el-col :xs="24" :sm="24" :md="12" :lg="12" :xl="12">
                <el-form-item label="">
                  <el-input 
                    v-model="filterForm.location" 
                    placeholder="" 
                    clearable
                  />
                </el-form-item>
              </el-col>
            </el-row>
            
            <div class="filter-actions">
              <el-button type="primary" @click="applyFilter"></el-button>
              <el-button @click="resetFilter"></el-button>
            </div>
          </el-form>
        </div>
      </el-card>
      
      <div class="records-wrapper">
        <el-card class="records-card">
          <div class="card-header">
            <h3></h3>
            <div class="card-actions">
              <el-button type="warning" @click="exportRecords">
                <el-icon><download /></el-icon> 
              </el-button>
            </div>
          </div>
          
          <el-table 
            :data="filteredRecords" 
            style="width: 100%" 
            border 
            v-loading="loading"
            stripe
            class="responsive-table"
            height="500"
            @scroll="handleScroll"
          >
            <el-table-column prop="waste_type_name" label="" min-width="110" />
            <el-table-column prop="location" label="" min-width="120" />
            <el-table-column label="" min-width="160">
              <template #default="scope">
                {{ parseFormattedDateTime(scope.row.collection_start_time) }}
              </template>
            </el-table-column>
            <el-table-column label="()" min-width="100">
              <template #default="scope">
                {{ parseFloat(scope.row.quantity).toFixed(3) }}
              </template>
            </el-table-column>
            <el-table-column label="" min-width="160" class="mobile-hidden">
              <template #default="scope">
                {{ parseFormattedDateTime(scope.row.created_at) }}
              </template>
            </el-table-column>
            <el-table-column label="" min-width="100" class="mobile-hidden">
              <template #default="scope">
                {{ scope.row.creator_name || '' }}
              </template>
            </el-table-column>
            <el-table-column
              label=""
              width="120"
              align="center"
            >
              <template #default="scope">
                <div v-if="scope.row.photo_path_before" class="photo-preview">
                  <!--  -->
                  <div 
                    v-for="(path, index) in parsePhotoPath(scope.row.photo_path_before)" 
                    :key="index"
                    class="photo-thumbnail-container"
                    @click="previewPhoto(parsePhotoPath(scope.row.photo_path_before), index)"
                  >
                    <el-image
                      style="width: 50px; height: 50px"
                      :src="`${baseUrl}${path}`"
                      fit="cover"
                    ></el-image>
                  </div>
                  <div v-if="parsePhotoPath(scope.row.photo_path_before).length > 1" class="photo-count">
                    {{ parsePhotoPath(scope.row.photo_path_before).length }}
                  </div>
                </div>
                <span v-else></span>
              </template>
            </el-table-column>
            <el-table-column
              label=""
              width="120"
              align="center"
            >
              <template #default="scope">
                <div v-if="scope.row.photo_path_after" class="photo-preview">
                  <!--  -->
                  <div 
                    v-for="(path, index) in parsePhotoPath(scope.row.photo_path_after)" 
                    :key="index"
                    class="photo-thumbnail-container"
                    @click="previewPhoto(parsePhotoPath(scope.row.photo_path_after), index)"
                  >
                    <el-image
                      style="width: 50px; height: 50px"
                      :src="`${baseUrl}${path}`"
                      fit="cover"
                    ></el-image>
                  </div>
                  <div v-if="parsePhotoPath(scope.row.photo_path_after).length > 1" class="photo-count">
                    {{ parsePhotoPath(scope.row.photo_path_after).length }}
                  </div>
                </div>
                <span v-else></span>
              </template>
            </el-table-column>
            <el-table-column label="" min-width="70" fixed="right">
              <template #default="scope">
                <div class="operation-buttons">
                  <el-button 
                    type="primary" 
                    size="small" 
                    @click="editRecord(scope.row.id)"
                    text
                    v-if="canEdit(scope.row)"
                  >
                    
                  </el-button>
                  <el-button 
                    type="danger" 
                    size="small" 
                    @click="confirmDelete(scope.row)"
                    text
                    v-if="canEdit(scope.row)"
                  >
                    
                  </el-button>
                </div>
              </template>
            </el-table-column>
          </el-table>
          
          <div v-if="loadingMore" class="loading-more">
            <el-icon class="loading"><loading /></el-icon>
            ...
          </div>
          
          <div class="empty-block" v-if="records.length === 0 && !loading">
            <el-empty description="" />
          </div>
        </el-card>
      </div>
    </div>

    <div class="footer">
      <p>&copy; 2025 </p>
    </div>

    <!--  -->
    <el-image-viewer
      v-if="showViewer"
      :url-list="previewImages"
      :initial-index="previewIndex"
      @close="closeViewer"
    />
  </div>
</template>

<script>
import { ref, onMounted, computed, reactive } from 'vue';
import { useRouter } from 'vue-router';
import { ElMessage, ElMessageBox, ElImageViewer } from 'element-plus';
import httpService from '../config/httpService';
import apiConfig from '../config/api';
import { ArrowLeft, HomeFilled, Refresh, Plus, User, ArrowDown, ArrowUp, Download, Loading } from '@element-plus/icons-vue';
import { exportToExcel, prepareImageExportData } from '../utils/exportUtils';
import auth from '../store/auth';

export default {
  name: 'RecordsList',
  components: {
    ArrowLeft,
    HomeFilled,
    Refresh,
    Plus,
    User,
    ArrowDown,
    ArrowUp,
    Download,
    ElImageViewer,
    Loading
  },
  props: {
    unitId: {
      type: [String, Number],
      required: true
    }
  },
  setup(props) {
    const router = useRouter();
    const records = ref([]);
    const loading = ref(false);
    const unitName = ref('');
    const wasteTypes = ref([]);
    const showFilterPanel = ref(true);
    
    // baseUrl
    const baseUrl = apiConfig.baseURL;
    
    // 
    const parsePhotoPath = (path) => {
      if (!path) return [];
      
      try {
        // JSON
        if (path.startsWith('[') && path.endsWith(']')) {
          return JSON.parse(path);
        }
        // JSON
        return [path];
      } catch (error) {
        console.error(':', error);
        // 
        return [path];
      }
    };
    
    // 
    const page = ref(1);
    const pageSize = ref(20);
    const hasMore = ref(true);
    const loadingMore = ref(false);
    
    // 
    const filterForm = reactive({
      dateRange: null,
      wasteTypeId: null,
      minQuantity: null,
      maxQuantity: null,
      location: ''
    });
    
    // 
    const filteredRecords = computed(() => {
      if (!records.value || !Array.isArray(records.value)) {
        return [];
      }
      
      return records.value.filter(record => {
        // 
        if (filterForm.wasteTypeId && record.waste_type_id !== filterForm.wasteTypeId) {
          return false;
        }
        
        // 
        if (filterForm.minQuantity !== null && parseFloat(record.quantity) < filterForm.minQuantity) {
          return false;
        }
        if (filterForm.maxQuantity !== null && parseFloat(record.quantity) > filterForm.maxQuantity) {
          return false;
        }
        
        // 
        if (filterForm.location && !record.location.toLowerCase().includes(filterForm.location.toLowerCase())) {
          return false;
        }
        
        // 
        if (filterForm.dateRange && filterForm.dateRange.length === 2) {
          const startDate = new Date(filterForm.dateRange[0]);
          const endDate = new Date(filterForm.dateRange[1]);
          endDate.setHours(23, 59, 59); // 
          
          const recordDate = new Date(record.collection_start_time);
          if (recordDate < startDate || recordDate > endDate) {
            return false;
          }
        }
        
        return true;
      });
    });
    
    // 
    const isAdmin = computed(() => {
      return auth.isAdmin();
    });
    
    // 
    const isUnitAdmin = computed(() => {
      return auth.isUnitAdmin();
    });
    
    // 
    const parseFormattedDateTime = (dateTimeStr) => {
      if (!dateTimeStr) return '';
      const date = new Date(dateTimeStr);
      return `${date.getFullYear()}-${String(date.getMonth() + 1).padStart(2, '0')}-${String(date.getDate()).padStart(2, '0')} ${String(date.getHours()).padStart(2, '0')}:${String(date.getMinutes()).padStart(2, '0')}`;
    };
    
    // 
    const refreshRecords = async () => {
      await fetchRecords();
      ElMessage.success('');
    };
    
    // 
    const goBack = () => {
      // ID
      const unitId = auth.getUnitId();
      if (unitId) {
        router.push(`/unit/${unitId}`);
      } else {
        // ID
        router.push('/');
      }
    };
    
    // 
    const goHome = () => {
      router.push('/');
    };
    
    // 
    const goToUserManagement = () => {
      router.push(`/user-management`);
    };
    
    // 
    const addNewRecord = () => {
      router.push(`/unit/${props.unitId}`);
    };
    
    // 
    const editRecord = (recordId) => {
      router.push(`/record/${recordId}`);
    };
    
    // 
    const canEdit = (record) => {
      if (!record) return false;
      
      // 
      if (isAdmin.value) return true;
      
      // 
      const unitId = auth.getUnitId();
      if (isUnitAdmin.value && unitId && record.unit_id === parseInt(unitId)) return true;
      
      // 
      const userId = auth.getUserId();
      return userId && record.creator_id === parseInt(userId);
    };
    
    // 
    const confirmDelete = (record) => {
      ElMessageBox.confirm(
        ``,
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning',
        }
      )
        .then(async () => {
          try {
            await httpService.delete(`${apiConfig.endpoints.wasteRecords}/${record.id}`);
            ElMessage.success('');
            await fetchRecords(); // 
          } catch (error) {
            console.error(':', error);
            ElMessage.error('');
          }
        })
        .catch(() => {
          ElMessage.info('');
        });
    };
    
    // 
    const applyFilter = async () => {
      try {
        // 
        page.value = 1;
        
        // 
        await fetchRecords();
        
        ElMessage.success('');
      } catch (error) {
        console.error(':', error);
        ElMessage.error('');
      }
    };
    
    // 
    const resetFilter = async () => {
      filterForm.dateRange = null;
      filterForm.wasteTypeId = null;
      filterForm.minQuantity = null;
      filterForm.maxQuantity = null;
      filterForm.location = '';
      
      // 
      page.value = 1;
      
      // 
      await fetchRecords();
      
      ElMessage.success('');
    };
    
    // 
    const exportRecords = async () => {
      try {
        loading.value = true;
        console.log('...');
        
        // 
        const recordsToExport = filteredRecords.value;
        
        console.log(`: ${recordsToExport.length}`);
        
        if (recordsToExport.length === 0) {
          ElMessage.warning('');
          loading.value = false;
          return;
        }
        
        // 
        const hasPhotos = recordsToExport.some(record => 
          record.photo_path_before || record.photo_path_after
        );
        console.log(':', hasPhotos);
        
        // 
        if (recordsToExport.length > 0) {
          const firstRecord = recordsToExport[0];
          console.log(':');
          console.log(':', firstRecord.photo_path_before);
          console.log(':', firstRecord.photo_path_after);
        }
        
        // URL
        console.log('baseUrl:', baseUrl);
        
        // 
        const exportData = recordsToExport.map(record => ({
          '': record.unit_name || '',
          '': record.waste_type_name || '',
          '': record.location || '',
          '': record.collection_start_time ? new Date(record.collection_start_time).toLocaleString() : '',
          '(kg)': record.quantity || 0,
          '': record.created_at ? new Date(record.created_at).toLocaleString() : '',
          '': record.creator_name || '',
          '': '',  // imageData
          '': ''   // imageData
        }));
        
        console.log('');
        
        // 
        console.log('...');
        // URL
        const imageData = await prepareImageExportData(recordsToExport, baseUrl);
        console.log(':', Object.keys(imageData).length);
        console.log(':', JSON.stringify(imageData));
        
        // 
        const fileName = `${unitName.value}`;
        const headers = [
          { text: '', field: '' },
          { text: '', field: '' },
          { text: '', field: '' },
          { text: '', field: '' },
          { text: '(kg)', field: '(kg)' },
          { text: '', field: '' },
          { text: '', field: '' },
          { text: '', field: '' },
          { text: '', field: '' }
        ];
        
        console.log('Excel...');
        try {
          const result = await exportToExcel(exportData, fileName, headers, imageData);
          
          if (result) {
            console.log('');
            ElMessage.success('');
          } else {
            console.log('CSV');
            ElMessage.warning('ExcelCSV');
          }
        } catch (exportError) {
          console.error('Excel:', exportError);
          ElMessage.error('Excel: ' + exportError.message);
        }
      } catch (error) {
        console.error(':', error);
        ElMessage.error(': ' + error.message);
      } finally {
        loading.value = false;
      }
    };
    
    // 
    const showViewer = ref(false);
    const previewImages = ref([]);
    const previewIndex = ref(0);
    
    // 
    const previewPhoto = (paths, index) => {
      // paths
      const photoArray = Array.isArray(paths) ? paths : [paths];
      previewImages.value = photoArray.map(path => `${baseUrl}${path}`);
      previewIndex.value = index || 0;
      showViewer.value = true;
    };
    
    // 
    const closeViewer = () => {
      showViewer.value = false;
    };

    onMounted(async () => {
      await fetchUnitName();
      await fetchWasteTypes();
      await fetchRecords();
    });

    // 
    const fetchWasteTypes = async () => {
      try {
        const response = await httpService.get(apiConfig.endpoints.wasteTypes);
        wasteTypes.value = response.data;
      } catch (error) {
        console.error('Error fetching waste types:', error);
        ElMessage.error('');
      }
    };
    
    const fetchUnitName = async () => {
      try {
        const response = await httpService.get(apiConfig.endpoints.units);
        const unit = response.data.find(u => u.id === parseInt(props.unitId));
        if (unit) {
          unitName.value = unit.name;
        }
      } catch (error) {
        console.error('Error fetching unit name:', error);
        ElMessage.error('');
      }
    };

    const fetchRecords = async (isLoadMore = false) => {
      if (!isLoadMore) {
        loading.value = true;
        page.value = 1;
        records.value = [];
      } else {
        loadingMore.value = true;
      }
      
      try {
        // 
        const params = {
          page: page.value,
          pageSize: pageSize.value
        };
        
        // 
        if (filterForm.wasteTypeId) params.wasteTypeId = filterForm.wasteTypeId;
        if (filterForm.minQuantity !== null) params.minQuantity = filterForm.minQuantity;
        if (filterForm.maxQuantity !== null) params.maxQuantity = filterForm.maxQuantity;
        if (filterForm.location) params.location = filterForm.location;
        if (filterForm.dateRange && filterForm.dateRange.length === 2) {
          params.dateRange = JSON.stringify(filterForm.dateRange);
        }
        
        // ID
        const userId = auth.getUserId();
        if (!userId) {
          throw new Error('');
        }
        
        // ID
        const response = await httpService.get(`${apiConfig.endpoints.wasteRecordsByUser}/${userId}`, { params });
        
        // records
        if (response.data && Array.isArray(response.data.records)) {
          if (isLoadMore) {
            records.value = [...records.value, ...response.data.records];
          } else {
            records.value = response.data.records;
          }
          
          hasMore.value = !!response.data.hasMore;
        } else {
          console.error('Invalid response format:', response.data);
          records.value = [];
          hasMore.value = false;
          ElMessage.error('');
        }
      } catch (error) {
        console.error('Error fetching records:', error);
        records.value = [];
        hasMore.value = false;
        ElMessage.error('');
      } finally {
        loading.value = false;
        loadingMore.value = false;
      }
    };
    
    // 
    const handleScroll = async (e) => {
      // e.target
      if (!e || !e.target) return;
      
      const scrollHeight = e.target.scrollHeight || 0;
      const scrollTop = e.target.scrollTop || 0;
      const clientHeight = e.target.clientHeight || 0;
      
      // 
      if (scrollHeight - scrollTop - clientHeight < 50 && hasMore.value && !loadingMore.value) {
        page.value++;
        await fetchRecords(true);
      }
    };

    return {
      records,
      filteredRecords,
      loading,
      unitName,
      wasteTypes,
      isAdmin,
      isUnitAdmin,
      parseFormattedDateTime,
      refreshRecords,
      goBack,
      goHome,
      goToUserManagement,
      addNewRecord,
      editRecord,
      canEdit,
      confirmDelete,
      apiConfig,
      // 
      showFilterPanel,
      filterForm,
      applyFilter,
      resetFilter,
      // 
      exportRecords,
      // 
      showViewer,
      previewImages,
      previewIndex,
      previewPhoto,
      closeViewer,
      handleScroll,
      // 
      parsePhotoPath,
      baseUrl,
      // 
      page,
      pageSize,
      hasMore,
      loadingMore
    };
  }
};
</script>

