<template>
  <div class="edit-record-container">
    <div class="header">
      <div class="back-button" @click="goBack">
        <el-icon><arrow-left /></el-icon> 
      </div>
      <h1>{{ isNew ? '' : '' }}</h1>
      <div></div>
    </div>

    <div class="content">
      <div class="form-header">
        <h2>{{ unitName }}</h2>
      </div>

      <el-form 
        ref="recordForm" 
        :model="form" 
        :rules="rules" 
        label-width="120px"
        class="record-form"
        v-loading="loading"
      >
        <!--  -->
        <el-form-item label="" prop="unitId" v-if="isSuperAdmin && isNew">
          <el-select v-model="form.unitId" placeholder="" style="width: 100%">
            <el-option 
              v-for="unit in units" 
              :key="unit.id" 
              :label="unit.name" 
              :value="unit.id" 
            />
          </el-select>
        </el-form-item>

        <el-form-item label="" prop="wasteTypeId">
          <el-select v-model="form.wasteTypeId" placeholder="" style="width: 100%">
            <el-option 
              v-for="type in wasteTypes" 
              :key="type.id" 
              :label="type.name" 
              :value="type.id" 
            />
          </el-select>
        </el-form-item>

        <el-form-item label="" prop="location">
          <el-input v-model="form.location" placeholder="" />
        </el-form-item>

        <el-form-item label="">
          <el-date-picker
            v-model="form.collectionDate"
            type="date"
            placeholder=""
            format="YYYY-MM-DD"
            value-format="YYYY-MM-DD"
            style="width: 100%"
          />
        </el-form-item>

        <el-form-item label="">
          <el-time-picker
            v-model="form.collectionTime"
            format="HH:mm"
            placeholder=""
            value-format="HH:mm"
            style="width: 100%"
          >
            <template #prefix>
              <el-icon><clock /></el-icon>
            </template>
          </el-time-picker>
          <!-- <div class="time-tip"></div> -->
        </el-form-item>

        <el-form-item label="()" prop="quantity">
          <el-input-number 
            v-model="form.quantity" 
            :min="0" 
            :precision="3" 
            :step="0.001" 
            style="width: 100%"
          />
        </el-form-item>

        <el-form-item label="" prop="photo_path_before">
          <div class="photo-tip">5</div>
          <div class="upload-warning" v-if="showLargeFileWarning">
            <el-alert
              title=""
              type="warning"
              :closable="false"
              show-icon
            />
          </div>
          <el-upload
            class="waste-photo-uploader"
            action="#"
            :auto-upload="false"
            :on-change="handlePhotoBeforeChange"
            :on-remove="handlePhotoBeforeRemove"
            :file-list="fileListBefore"
            :limit="5"
            multiple
            list-type="picture-card"
            :on-preview="handlePictureCardPreview"
            :before-upload="handleBeforeUpload"
            accept="image/jpeg,image/jpg,image/png,image/gif,image/bmp,image/webp,.jpeg,.jpg,.png,.gif,.bmp,.webp"
          >
            <el-icon><plus /></el-icon>
          </el-upload>
        </el-form-item>

        <el-form-item label="" prop="photo_path_after">
          <div class="photo-tip">5</div>
          <el-upload
            class="waste-photo-uploader"
            action="#"
            :auto-upload="false"
            :on-change="handlePhotoAfterChange"
            :on-remove="handlePhotoAfterRemove"
            :file-list="fileListAfter"
            :limit="5"
            multiple
            list-type="picture-card"
            :on-preview="handlePictureCardPreview"
            :before-upload="handleBeforeUpload"
            accept="image/jpeg,image/jpg,image/png,image/gif,image/bmp,image/webp,.jpeg,.jpg,.png,.gif,.bmp,.webp"
          >
            <el-icon><plus /></el-icon>
          </el-upload>
          
          <!--  -->
          <el-image-viewer
            v-if="showViewer"
            :url-list="previewImages"
            :initial-index="previewIndex"
            @close="closeViewer"
          />
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="submitForm" :loading="loading"></el-button>
          <el-button @click="goBack"></el-button>
        </el-form-item>
        
        <!--  -->
        <el-collapse v-if="!isNew">
          <el-collapse-item title="">
            <div>
              <h4>:</h4>
              <div v-for="(file, index) in fileListBefore" :key="'before-'+index">
                <div>: {{ file.name }}</div>
                <div>URL: {{ file.url }}</div>
                <div>
                  <img :src="file.url" style="max-width: 200px; max-height: 200px;" />
                </div>
                <hr />
              </div>
              
              <h4>:</h4>
              <div v-for="(file, index) in fileListAfter" :key="'after-'+index">
                <div>: {{ file.name }}</div>
                <div>URL: {{ file.url }}</div>
                <div>
                  <img :src="file.url" style="max-width: 200px; max-height: 200px;" />
                </div>
                <hr />
              </div>
            </div>
          </el-collapse-item>
        </el-collapse>
      </el-form>
    </div>

    <!--  -->
    <el-dialog
      v-model="showUploadProgress"
      title=""
      width="30%"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
      :show-close="false"
    >
      <div class="upload-progress">
        <p>...</p>
        <el-progress 
          :percentage="uploadPercentage" 
          :format="percentageFormat"
          :status="uploadPercentage === 100 ? 'success' : ''"
        ></el-progress>
        <p class="upload-status">{{ uploadStatus }}</p>
      </div>
    </el-dialog>

    <div class="footer">
      <p>&copy; 2025 </p>
    </div>
  </div>
</template>

<script>
import { ref, reactive, onMounted, computed } from 'vue';
import { useRouter, useRoute } from 'vue-router';
import { ElMessage, ElImageViewer } from 'element-plus';
import httpService from '../config/httpService';
import apiConfig from '../config/api';
import { ArrowLeft, Plus, Clock } from '@element-plus/icons-vue';
import auth from '../store/auth';

export default {
  name: 'EditRecordView',
  components: {
    ArrowLeft,
    Plus,
    Clock,
    ElImageViewer
  },
  setup() {
    const router = useRouter();
    const route = useRoute();
    const recordForm = ref(null);
    const loading = ref(false);
    const submitting = ref(false);
    const unitName = ref('');
    const wasteTypes = ref([]);
    const units = ref([]);
    const fileListBefore = ref([]);
    const fileListAfter = ref([]);
    const previewImages = ref([]);
    const showViewer = ref(false);
    const previewIndex = ref(0);
    const createdAt = ref('');
    const showUploadProgress = ref(false);
    const uploadPercentage = ref(0);
    const uploadStatus = ref('...');
    const showLargeFileWarning = ref(false);
    
    // 
    const parsePhotoPath = (path) => {
      if (!path) return [];
      
      console.log(':', path, ':', typeof path);
      
      try {
        // 
        if (Array.isArray(path)) {
          console.log(':', path);
          return path;
        }
        
        // JSON
        if (typeof path === 'string') {
          // JSON
          if (path.startsWith('[') && path.endsWith(']')) {
            const parsed = JSON.parse(path);
            console.log('JSON:', parsed);
            return parsed;
          }
          
          // 
          if (path.includes(',')) {
            const paths = path.split(',').map(p => p.trim()).filter(p => p);
            console.log(':', paths);
            return paths;
          }
          
          // 
          console.log(':', [path]);
          return [path];
        }
        
        // 
        console.log(':', String(path));
        return [String(path)];
      } catch (error) {
        console.error(':', error);
        // 
        return typeof path === 'string' ? [path] : [];
      }
    };
    
    // 
    const isNew = computed(() => {
      return !route.params.id || route.params.id === 'new';
    });
    
    // 
    const isSuperAdmin = computed(() => {
      return auth.state.isLoggedIn && auth.state.user.role_id === 3;
    });
    
    // 
    const form = reactive({
      unitId: '',
      wasteTypeId: '',
      location: '',
      collectionDate: '',
      collectionTime: '',
      quantity: 0,
      recordId: null,
      creatorId: auth.state.user?.id || null,
      photo_path_before: '',
      photo_path_after: '',
      remarks: ''
    });

    const rules = {
      unitId: [
        { required: true, message: '', trigger: 'change' }
      ],
      wasteTypeId: [
        { required: true, message: '', trigger: 'change' }
      ],
      location: [
        { required: true, message: '', trigger: 'blur' }
      ],
      collectionDate: [
        { required: false }
      ],
      collectionTime: [
        { required: false }
      ],
      quantity: [
        { required: true, message: '', trigger: 'change' }
      ]
    };

    onMounted(async () => {
      loading.value = true;
      
      try {
        // 
        await fetchWasteTypes();
        
        // 
        if (isSuperAdmin.value) {
          await fetchUnits();
        }
        
        // 
        if (isNew.value) {
          // 
          if (!isSuperAdmin.value && auth.state.user) {
            form.unitId = auth.state.user.unit_id;
            await fetchUnitName(form.unitId);
          }
        } else {
          // 
          await fetchRecordDetails();
        }
      } catch (error) {
        console.error(':', error);
        ElMessage.error('');
      } finally {
        loading.value = false;
      }
    });

    // 
    const fetchUnits = async () => {
      try {
        const response = await httpService.get(apiConfig.endpoints.units);
        units.value = response.data;
      } catch (error) {
        console.error(':', error);
        ElMessage.error('');
      }
    };

    // 
    const fetchUnitName = async (unitId) => {
      try {
        const response = await httpService.get(apiConfig.endpoints.units);
        const unit = response.data.find(u => u.id === parseInt(unitId));
        if (unit) {
          unitName.value = unit.name;
        }
      } catch (error) {
        console.error(':', error);
      }
    };

    // 
    const fetchWasteTypes = async () => {
      try {
        const response = await httpService.get(apiConfig.endpoints.wasteTypes);
        wasteTypes.value = response.data;
      } catch (error) {
        console.error(':', error);
        ElMessage.error('');
      }
    };

    // 
    const fetchRecordDetails = async () => {
      try {
        loading.value = true;
        const response = await httpService.get(`${apiConfig.endpoints.wasteRecords}/detail/${route.params.id}`);
        
        const record = response.data;
        console.log(':', record);
        
        form.unitId = record.unit_id;
        form.wasteTypeId = record.waste_type_id;
        form.location = record.location;
        form.recordId = record.id;
        
        // 
        if (record.collection_start_time) {
          const dateTime = new Date(record.collection_start_time);
          form.collectionDate = dateTime.toISOString().slice(0, 10);
          form.collectionTime = dateTime.toTimeString().slice(0, 5);
        }
        
        form.quantity = record.quantity;
        
        // 
        if (record.photo_path_before) {
          console.log(':', record.photo_path_before);
          const photoPaths = parsePhotoPath(record.photo_path_before);
          console.log(':', photoPaths);
          
          fileListBefore.value = photoPaths.map(path => {
            // URL
            let fullUrl = path;
            if (!path.startsWith('http')) {
              // 
              const cleanPath = path.startsWith('/') ? path.substring(1) : path;
              fullUrl = `${window.location.origin}/${cleanPath}`;
            }
            
            console.log('URL:', fullUrl);
            
            return {
              name: path.split('/').pop(),
              url: fullUrl
            };
          });
        }
        
        if (record.photo_path_after) {
          console.log(':', record.photo_path_after);
          const photoPaths = parsePhotoPath(record.photo_path_after);
          console.log(':', photoPaths);
          
          fileListAfter.value = photoPaths.map(path => {
            // URL
            let fullUrl = path;
            if (!path.startsWith('http')) {
              // 
              const cleanPath = path.startsWith('/') ? path.substring(1) : path;
              fullUrl = `${window.location.origin}/${cleanPath}`;
            }
            
            console.log('URL:', fullUrl);
            
            return {
              name: path.split('/').pop(),
              url: fullUrl
            };
          });
        }
        
        // 
        unitName.value = record.unit_name;
        
        // 
        createdAt.value = record.created_at;
        
        // 
        updatePreviewImages([...fileListBefore.value, ...fileListAfter.value]);
        
      } catch (error) {
        console.error(':', error);
        ElMessage.error('');
      } finally {
        loading.value = false;
      }
    };

    // 
    const handleBeforeUpload = async (file) => {
      // 
      const acceptedTypes = ['image/jpeg', 'image/jpg', 'image/png', 'image/gif', 'image/bmp', 'image/webp'];
      const isImage = acceptedTypes.includes(file.type);
      
      if (!isImage) {
        ElMessage.error('!');
        return false;
      }

      // 50MB = 50 * 1024 * 1024 bytes
      const maxSize = 50 * 1024 * 1024;
      if (file.size > maxSize) {
        ElMessage.error('50MB!');
        return false;
      }
      
      // 
      const largeFileThreshold = 2 * 1024 * 1024; // 2MB
      if (file.size > largeFileThreshold) {
        showLargeFileWarning.value = true;
      }
      
      // true
      return true;
    };

    // 
    const handlePhotoBeforeChange = (file, fileList) => {
      console.log(':', file);
      
      // 
      checkForLargeFiles(fileList);
      
      // raw
      if (file.raw === undefined && file.status === 'ready') {
        file.raw = file.originFileObj || file;
        console.log('raw:', file.raw);
      }
      
      // 
      fileListBefore.value = fileList;
      
      // 
      updatePreviewImages([...fileListBefore.value, ...fileListAfter.value]);
      
      return false; // 
    };

    // 
    const handlePhotoBeforeRemove = (file, fileList) => {
      console.log(':', file);
      
      // fileListBefore
      fileListBefore.value = fileList;
      
      // 
      if (!checkForLargeFiles([...fileListBefore.value, ...fileListAfter.value])) {
        showLargeFileWarning.value = false;
      }
      
      // 
      updatePreviewImages([...fileListBefore.value, ...fileListAfter.value]);
    };

    // 
    const handlePhotoAfterChange = (file, fileList) => {
      console.log(':', file);
      
      // 
      checkForLargeFiles(fileList);
      
      // raw
      if (file.raw === undefined && file.status === 'ready') {
        file.raw = file.originFileObj || file;
        console.log('raw:', file.raw);
      }
      
      // 
      fileListAfter.value = fileList;
      
      // 
      updatePreviewImages([...fileListBefore.value, ...fileListAfter.value]);
      
      return false; // 
    };

    // 
    const handlePhotoAfterRemove = (file, fileList) => {
      console.log(':', file);
      
      // fileListAfter
      fileListAfter.value = fileList;
      
      // 
      if (!checkForLargeFiles([...fileListBefore.value, ...fileListAfter.value])) {
        showLargeFileWarning.value = false;
      }
      
      // 
      updatePreviewImages([...fileListBefore.value, ...fileListAfter.value]);
    };

    // 
    const updatePreviewImages = (fileList) => {
      previewImages.value = fileList.map(file => {
        if (file.url) {
          console.log('URL:', file.url);
          return file.url;
        } else if (file.raw) {
          // Blob URL
          if (!file._blobUrl) {
            file._blobUrl = URL.createObjectURL(file.raw);
          }
          console.log('raw:', file._blobUrl);
          return file._blobUrl;
        } else if (file instanceof File) {
          // Blob URL
          if (!file._blobUrl) {
            file._blobUrl = URL.createObjectURL(file);
          }
          console.log('File:', file._blobUrl);
          return file._blobUrl;
        }
        return '';
      }).filter(url => url);
      
      console.log(':', previewImages.value);
    };

    // 
    const handlePictureCardPreview = (file) => {
      console.log(':', file);
      
      // 
      updatePreviewImages([...fileListBefore.value, ...fileListAfter.value]);
      
      // 
      const index = previewImages.value.findIndex(url => {
        if (file.url) {
          return url === file.url;
        } else if (file.raw) {
          // Blob URL
          const blobUrl = URL.createObjectURL(file.raw);
          const result = url === blobUrl;
          // Blob URL
          URL.revokeObjectURL(blobUrl);
          return result;
        }
        return false;
      });
      
      if (index !== -1) {
        previewIndex.value = index;
      } else {
        previewIndex.value = 0;
      }
      
      // 
      showViewer.value = true;
    };
    
    // 
    const closeViewer = () => {
      showViewer.value = false;
    };

    // 
    const checkForLargeFiles = (files) => {
      const largeFileThreshold = 2 * 1024 * 1024; // 2MB
      return files.some(file => file.size > largeFileThreshold);
    };

    // 
    const handleUploadProgress = (progressEvent) => {
      if (progressEvent.total) {
        const percentage = Math.round((progressEvent.loaded * 100) / progressEvent.total);
        uploadPercentage.value = percentage;
        
        if (percentage < 33) {
          uploadStatus.value = '...';
        } else if (percentage < 66) {
          uploadStatus.value = '...';
        } else if (percentage < 100) {
          uploadStatus.value = '...';
        } else {
          uploadStatus.value = '...';
        }
      }
    };

    // 
    const percentageFormat = (percentage) => {
      return percentage === 100 ? '' : `${percentage}%`;
    };

    // 
    const submitForm = async () => {
      if (!recordForm.value) return;
      
      await recordForm.value.validate(async (valid) => {
        if (valid) {
          // 
          uploadPercentage.value = 0;
          uploadStatus.value = '...';
          showUploadProgress.value = true;
          
          try {
            const formData = new FormData();
            formData.append('unitId', form.unitId);
            formData.append('wasteTypeId', form.wasteTypeId);
            formData.append('location', form.location);
            formData.append('collectionDate', form.collectionDate);
            formData.append('collectionTime', form.collectionTime);
            formData.append('quantity', form.quantity);
            formData.append('remarks', form.remarks || '');
            
            //  - 
            // 
            
            // 
            if (fileListBefore.value.length > 0) {
              const newFiles = fileListBefore.value.filter(file => file.raw);
              const existingFiles = fileListBefore.value.filter(file => !file.raw);
              
              // URLFile
              if (newFiles.length > 0) {
                console.log('');
                
                // formData
                newFiles.forEach(file => {
                  if (file.raw) {
                    console.log(':', file.raw.name);
                    formData.append('photo_before', file.raw);
                  }
                });
                
                // photo_path_before
                if (existingFiles.length > 0) {
                  const existingPaths = existingFiles.map(file => {
                    // URL
                    let path = file.url;
                    const origin = window.location.origin;
                    if (path.startsWith(origin)) {
                      path = path.substring(origin.length);
                      // /
                      if (!path.startsWith('/')) {
                        path = '/' + path;
                      }
                    }
                    return path;
                  });
                  
                  console.log(':', existingPaths);
                  formData.append('photo_path_before', JSON.stringify(existingPaths));
                }
              } else if (existingFiles.length > 0) {
                // photo_path_before
                const existingPaths = existingFiles.map(file => {
                  // URL
                  let path = file.url;
                  const origin = window.location.origin;
                  if (path.startsWith(origin)) {
                    path = path.substring(origin.length);
                    // /
                    if (!path.startsWith('/')) {
                      path = '/' + path;
                    }
                  }
                  return path;
                });
                
                console.log('():', existingPaths);
                formData.append('photo_path_before', JSON.stringify(existingPaths));
              }
            } else {
              // 
              formData.append('photo_path_before', JSON.stringify([]));
            }
            
            // 
            if (fileListAfter.value.length > 0) {
              const newFiles = fileListAfter.value.filter(file => file.raw);
              const existingFiles = fileListAfter.value.filter(file => !file.raw);
              
              // URLFile
              if (newFiles.length > 0) {
                console.log('');
                
                // formData
                newFiles.forEach(file => {
                  if (file.raw) {
                    console.log(':', file.raw.name);
                    formData.append('photo_after', file.raw);
                  }
                });
                
                // photo_path_after
                if (existingFiles.length > 0) {
                  const existingPaths = existingFiles.map(file => {
                    // URL
                    let path = file.url;
                    const origin = window.location.origin;
                    if (path.startsWith(origin)) {
                      path = path.substring(origin.length);
                      // /
                      if (!path.startsWith('/')) {
                        path = '/' + path;
                      }
                    }
                    return path;
                  });
                  
                  console.log(':', existingPaths);
                  formData.append('photo_path_after', JSON.stringify(existingPaths));
                }
              } else if (existingFiles.length > 0) {
                // photo_path_after
                const existingPaths = existingFiles.map(file => {
                  // URL
                  let path = file.url;
                  const origin = window.location.origin;
                  if (path.startsWith(origin)) {
                    path = path.substring(origin.length);
                    // /
                    if (!path.startsWith('/')) {
                      path = '/' + path;
                    }
                  }
                  return path;
                });
                
                console.log('():', existingPaths);
                formData.append('photo_path_after', JSON.stringify(existingPaths));
              }
            } else {
              // 
              formData.append('photo_path_after', JSON.stringify([]));
            }
            
            // FormData
            console.log('FormData:');
            for (let [key, value] of formData.entries()) {
              if (value instanceof File) {
                console.log(`${key}: File - ${value.name} (${value.type}, ${value.size} bytes)`);
              } else {
                console.log(`${key}: ${value}`);
              }
            }
            
            // API
            if (form.recordId) {
              await httpService.putForm(
                `${apiConfig.endpoints.wasteRecords}/${form.recordId}`, 
                formData,
                handleUploadProgress
              );
              ElMessage.success('');
            } else {
              await httpService.postForm(
                apiConfig.endpoints.wasteRecords, 
                formData,
                handleUploadProgress
              );
              ElMessage.success('');
            }
            
            // 
            showUploadProgress.value = false;
            
            // 
            goBack();
          } catch (error) {
            console.error(':', error);
            if (error.response && error.response.data) {
              console.error(':', error.response.data);
              ElMessage.error(`: ${error.response.data.error || ''}`);
            } else {
              ElMessage.error('');
            }
            showUploadProgress.value = false;
          }
        } else {
          console.log('');
          ElMessage.error('');
          return false;
        }
      });
    };

    // 
    const goBack = () => {
      if (isSuperAdmin.value) {
        router.push('/admin-records');
      } else {
        router.push({ 
          name: 'RecordsList', 
          params: { unitId: auth.state.user.unit_id } 
        });
      }
    };

    return {
      form,
      rules,
      recordForm,
      loading,
      submitting,
      unitName,
      wasteTypes,
      units,
      fileListBefore,
      fileListAfter,
      previewImages,
      showViewer,
      previewIndex,
      isNew,
      isSuperAdmin,
      parsePhotoPath,
      handlePhotoBeforeChange,
      handlePhotoBeforeRemove,
      handlePhotoAfterChange,
      handlePhotoAfterRemove,
      handlePictureCardPreview,
      handleBeforeUpload,
      closeViewer,
      submitForm,
      goBack,
      showUploadProgress,
      uploadPercentage,
      uploadStatus,
      percentageFormat,
      showLargeFileWarning
    };
  }
};
</script>

