<template>
  <div class="waste-form-container">
    <div class="header">
      <div class="back-button" @click="goBack" v-if="isAdmin">
        <el-icon><arrow-left /></el-icon> 
      </div>
      <div v-else></div>
      <h1></h1>
      <div class="view-records" @click="viewRecords">
         <el-icon><document /></el-icon>
      </div>
    </div>

    <div class="content">
      <div class="unit-info">
        <h2>{{ unitName }}</h2>
      </div>

      <el-form 
        ref="wasteForm" 
        :model="form" 
        :rules="rules" 
        label-position="top"
        class="waste-form"
      >
        <el-form-item label="" prop="wasteTypeId">
          <el-select v-model="form.wasteTypeId" placeholder="" style="width: 100%">
            <el-option 
              v-for="type in wasteTypes" 
              :key="type.id" 
              :label="type.name" 
              :value="type.id" 
            />
          </el-select>
        </el-form-item>

        <el-form-item label="" prop="location">
          <el-input v-model="form.location" placeholder="" />
        </el-form-item>

        <div class="form-row">
          <el-form-item label="" class="date-item">
            <el-date-picker
              v-model="form.collectionDate"
              type="date"
              placeholder=""
              format="YYYY-MM-DD"
              value-format="YYYY-MM-DD"
              style="width: 100%"
              :editable="false"
              popper-class="date-picker-popup"
              :teleported="false"
            />
          </el-form-item>

          <el-form-item label="" class="time-item">
            <el-time-picker
              v-model="form.collectionTime"
              format="HH:mm"
              placeholder=""
              value-format="HH:mm"
              style="width: 100%"
              :editable="false"
              popper-class="time-picker-popup"
              :teleported="false"
            >
              <template #prefix>
                <el-icon><clock /></el-icon>
              </template>
            </el-time-picker>
          </el-form-item>
        </div>

        <el-form-item label="()" prop="quantity">
          <el-input-number
            v-model="form.quantity"
            :min="0"
            :precision="3"
            :step="0.001"
            style="width: 100%"
            @focus="selectAllText($event)"
            :input-props="{
              inputmode: 'decimal',
              pattern: '[0-9]*[.,]?[0-9]*'
            }"
            :controls="false"
          />
        </el-form-item>

        <el-form-item label="" prop="photo_before">
          <div class="photo-tip">5</div>
          <div class="upload-warning" v-if="showLargeFileWarning">
            <el-alert
              title=""
              type="warning"
              :closable="false"
              show-icon
            />
          </div>
          <el-upload
            class="waste-photo-uploader"
            action="#"
            :auto-upload="false"
            :on-change="handlePhotoBeforeChange"
            :on-remove="handlePhotoBeforeRemove"
            :file-list="fileListBefore"
            :limit="5"
            multiple
            list-type="picture-card"
            :before-upload="handleBeforeUpload"
            accept="image/jpeg,image/jpg,image/png,image/gif,image/bmp,image/webp,.jpeg,.jpg,.png,.gif,.bmp,.webp"
          >
            <el-icon><plus /></el-icon>
          </el-upload>
        </el-form-item>

        <el-form-item label="" prop="photo_after">
          <div class="photo-tip">5</div>
          <el-upload
            class="waste-photo-uploader"
            action="#"
            :auto-upload="false"
            :on-change="handlePhotoAfterChange"
            :on-remove="handlePhotoAfterRemove"
            :file-list="fileListAfter"
            :limit="5"
            multiple
            list-type="picture-card"
            :before-upload="handleBeforeUpload"
            accept="image/jpeg,image/jpg,image/png,image/gif,image/bmp,image/webp,.jpeg,.jpg,.png,.gif,.bmp,.webp"
          >
            <el-icon><plus /></el-icon>
          </el-upload>
        </el-form-item>

        <div class="form-actions">
          <el-button type="primary" class="submit-btn" @click="submitForm" :loading="loading"></el-button>
          <el-button class="reset-btn" @click="resetForm"></el-button>
        </div>
      </el-form>
    </div>

    <!--  -->
    <el-dialog
      v-model="showUploadProgress"
      title=""
      width="30%"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
      :show-close="false"
    >
      <div class="upload-progress">
        <p>...</p>
        <el-progress 
          :percentage="uploadPercentage" 
          :format="percentageFormat"
          :status="uploadPercentage === 100 ? 'success' : ''"
        ></el-progress>
        <p class="upload-status">{{ uploadStatus }}</p>
      </div>
    </el-dialog>

    <div class="footer">
      <p>&copy; 2025 </p>
    </div>
  </div>
</template>

<script>
import { ref, reactive, onMounted, computed, onBeforeUnmount } from 'vue';
import { useRouter } from 'vue-router';
import { ElMessage } from 'element-plus';
import httpService from '../config/httpService';
import apiConfig from '../config/api';
import { ArrowLeft, Document, Plus, Clock } from '@element-plus/icons-vue';
import auth from '../store/auth';

export default {
  name: 'WasteForm',
  components: {
    ArrowLeft,
    Document,
    Plus,
    Clock
  },
  props: {
    id: {
      type: [String, Number],
      required: true
    }
  },
  setup(props) {
    const router = useRouter();
    const wasteForm = ref(null);
    const loading = ref(false);
    const unitName = ref('');
    const wasteTypes = ref([]);
    const photoFilesBefore = ref([]);
    const photoFilesAfter = ref([]);
    const fileListBefore = ref([]);
    const fileListAfter = ref([]);
    const showUploadProgress = ref(false);
    const uploadPercentage = ref(0);
    const uploadStatus = ref('...');
    const showLargeFileWarning = ref(false);
    
    // 
    const isAdmin = computed(() => {
      return auth.state.isLoggedIn && auth.state.user.role_id === 3;
    });

    // 
    const form = reactive({
      wasteTypeId: '',
      location: '',
      collectionDate: new Date().toISOString().slice(0, 10), // 
      collectionTime: '08:00',
      quantity: undefined,
      photo_before: [],
      photo_after: []
    });

    const rules = {
      wasteTypeId: [
        { required: true, message: '', trigger: 'change' }
      ],
      location: [
        { required: true, message: '', trigger: 'blur' }
      ],
      collectionDate: [
        { required: false }
      ],
      collectionTime: [
        { required: false }
      ],
      quantity: [
        { required: true, message: '', trigger: 'change' }
      ]
    };

    onMounted(async () => {
      // viewport meta
      const viewportMeta = document.createElement('meta');
      viewportMeta.setAttribute('name', 'viewport');
      viewportMeta.setAttribute('content', 'width=device-width,initial-scale=1.0,user-scalable=no,maximum-scale=1');
      document.head.appendChild(viewportMeta);

      // viewport meta
      const originalViewport = document.querySelector('meta[name="viewport"]');
      if (originalViewport && originalViewport !== viewportMeta) {
        originalViewport.remove();
      }

      await fetchUnitName();
      await fetchWasteTypes();
    });

    onBeforeUnmount(() => {
      // viewport meta
      const viewportMeta = document.querySelector('meta[name="viewport"]');
      if (viewportMeta) {
        viewportMeta.remove();
      }

      // viewport
      const originalViewport = document.createElement('meta');
      originalViewport.setAttribute('name', 'viewport');
      originalViewport.setAttribute('content', 'width=device-width,initial-scale=1.0');
      document.head.appendChild(originalViewport);
    });

    const fetchUnitName = async () => {
      try {
        const response = await httpService.get(apiConfig.endpoints.units);
        const unit = response.data.find(u => u.id === parseInt(props.id));
        if (unit) {
          unitName.value = unit.name;
        }
      } catch (error) {
        console.error('Error fetching unit name:', error);
        ElMessage.error('');
      }
    };

    const fetchWasteTypes = async () => {
      try {
        const response = await httpService.get(apiConfig.endpoints.wasteTypes);
        wasteTypes.value = response.data;
      } catch (error) {
        console.error('Error fetching waste types:', error);
        ElMessage.error('');
      }
    };

    // 
    const handleBeforeUpload = async (file) => {
      // 
      const acceptedTypes = ['image/jpeg', 'image/jpg', 'image/png', 'image/gif', 'image/bmp', 'image/webp'];
      const isImage = acceptedTypes.includes(file.type);
      
      if (!isImage) {
        ElMessage.error('!');
        return false;
      }

      // 50MB = 50 * 1024 * 1024 bytes
      const maxSize = 50 * 1024 * 1024;
      if (file.size > maxSize) {
        ElMessage.error('50MB!');
        return false;
      }
      
      // 
      const largeFileThreshold = 5 * 1024 * 1024; // 5MB
      if (file.size > largeFileThreshold) {
        showLargeFileWarning.value = true;
      }
      
      // true
      return true;
    };

    // 
    const handlePhotoBeforeChange = (file, fileList) => {
      // 
      console.log(':', file);
      fileListBefore.value = fileList;
      
      // 
      const newFiles = fileList.filter(f => f.raw && !photoFilesBefore.value.some(pf => pf.name === f.raw.name));
      
      // photoFilesBefore
      newFiles.forEach(newFile => {
        if (newFile.raw) {
          photoFilesBefore.value.push(newFile.raw);
        }
      });
      
      // 
      const allFiles = [...photoFilesBefore.value, ...photoFilesAfter.value];
      showLargeFileWarning.value = checkForLargeFiles(allFiles);
      
      console.log('photoFilesBefore:', photoFilesBefore.value);
    };

    // 
    const handlePhotoBeforeRemove = (file, fileList) => {
      console.log(':', file);
      fileListBefore.value = fileList;
      
      // photoFilesBefore
      if (file.raw) {
        photoFilesBefore.value = photoFilesBefore.value.filter(f => 
          f.name !== file.raw.name
        );
      } else {
        // rawuidname
        photoFilesBefore.value = photoFilesBefore.value.filter(f => 
          f.uid !== file.uid && f.name !== file.name
        );
      }
      
      // 
      if (!checkForLargeFiles([...photoFilesBefore.value, ...photoFilesAfter.value])) {
        showLargeFileWarning.value = false;
      }
      
      console.log('photoFilesBefore:', photoFilesBefore.value);
    };

    // 
    const handlePhotoAfterChange = (file, fileList) => {
      // 
      console.log(':', file);
      fileListAfter.value = fileList;
      
      // 
      const newFiles = fileList.filter(f => f.raw && !photoFilesAfter.value.some(pf => pf.name === f.raw.name));
      
      // photoFilesAfter
      newFiles.forEach(newFile => {
        if (newFile.raw) {
          photoFilesAfter.value.push(newFile.raw);
        }
      });
      
      // 
      const allFiles = [...photoFilesBefore.value, ...photoFilesAfter.value];
      showLargeFileWarning.value = checkForLargeFiles(allFiles);
      
      console.log('photoFilesAfter:', photoFilesAfter.value);
    };

    // 
    const handlePhotoAfterRemove = (file, fileList) => {
      console.log(':', file);
      fileListAfter.value = fileList;
      
      // photoFilesAfter
      if (file.raw) {
        photoFilesAfter.value = photoFilesAfter.value.filter(f => 
          f.name !== file.raw.name
        );
      } else {
        // rawuidname
        photoFilesAfter.value = photoFilesAfter.value.filter(f => 
          f.uid !== file.uid && f.name !== file.name
        );
      }
      
      // 
      if (!checkForLargeFiles([...photoFilesBefore.value, ...photoFilesAfter.value])) {
        showLargeFileWarning.value = false;
      }
      
      console.log('photoFilesAfter:', photoFilesAfter.value);
    };

    // 
    const checkForLargeFiles = (files) => {
      const largeFileThreshold = 5 * 1024 * 1024; // 5MB
      return files.some(file => file.size > largeFileThreshold);
    };

    // 
    const handleUploadProgress = (progressEvent) => {
      if (progressEvent.total) {
        const percentage = Math.round((progressEvent.loaded * 100) / progressEvent.total);
        uploadPercentage.value = percentage;
        
        if (percentage < 33) {
          uploadStatus.value = '...';
        } else if (percentage < 66) {
          uploadStatus.value = '...';
        } else if (percentage < 100) {
          uploadStatus.value = '...';
        } else {
          uploadStatus.value = '...';
        }
      }
    };

    // 
    const percentageFormat = (percentage) => {
      return percentage === 100 ? '' : `${percentage}%`;
    };

    const submitForm = () => {
      wasteForm.value.validate(async (valid) => {
        if (valid) {
          loading.value = true;
          try {
            const formData = new FormData();
            formData.append('unitId', props.id);
            formData.append('wasteTypeId', form.wasteTypeId);
            formData.append('location', form.location);
            
            // 
            if (form.collectionDate && form.collectionTime) {
              formData.append('collectionDate', form.collectionDate);
              formData.append('collectionTime', form.collectionTime);
            }
            formData.append('quantity', form.quantity);
            
            // ID
            if (auth.state.isLoggedIn && auth.state.user) {
              formData.append('creator_id', auth.state.user.id);
              // usernamephone
              const creatorName = auth.state.user.username || auth.state.user.phone || '';
              formData.append('creator_name', creatorName);
              console.log(':', {
                creator_id: auth.state.user.id,
                creator_name: creatorName,
                user: auth.state.user
              });
            } else {
              console.log('');
            }
            
            console.log(':', {
              unitId: props.id,
              wasteTypeId: form.wasteTypeId,
              location: form.location,
              quantity: form.quantity,
              photo_before: photoFilesBefore.value ? photoFilesBefore.value.length : 0,
              photo_after: photoFilesAfter.value ? photoFilesAfter.value.length : 0
            });
            
            // 
            if (photoFilesBefore.value && photoFilesBefore.value.length > 0) {
              console.log(':', photoFilesBefore.value.length);
              photoFilesBefore.value.forEach((file, index) => {
                if (file) {
                  console.log(` ${index+1}:`, file.name);
                  formData.append('photo_before', file);
                }
              });
            }
            
            // 
            if (photoFilesAfter.value && photoFilesAfter.value.length > 0) {
              console.log(':', photoFilesAfter.value.length);
              photoFilesAfter.value.forEach((file, index) => {
                if (file) {
                  console.log(` ${index+1}:`, file.name);
                  formData.append('photo_after', file);
                }
              });
            }

            // 
            const allFiles = [...(photoFilesBefore.value || []), ...(photoFilesAfter.value || [])];
            showLargeFileWarning.value = checkForLargeFiles(allFiles);
            
            // 
            if (allFiles.length > 0) {
              showUploadProgress.value = true;
              uploadPercentage.value = 0;
              uploadStatus.value = '...';
            }

            const response = await httpService.postForm(
              apiConfig.endpoints.wasteRecords, 
              formData,
              handleUploadProgress
            );
            
            console.log(':', response.data);

            ElMessage.success('');
            resetForm();
          } catch (error) {
            console.error('Error submitting form:', error);
            if (error.response) {
              console.error(':', error.response.data);
              console.error(':', error.response.status);
            }
            ElMessage.error('');
          } finally {
            loading.value = false;
            showUploadProgress.value = false;
          }
        } else {
          ElMessage.warning('');
        }
      });
    };

    const resetForm = () => {
      if (wasteForm.value) {
        wasteForm.value.resetFields();
      }
      photoFilesBefore.value = [];
      photoFilesAfter.value = [];
      fileListBefore.value = [];
      fileListAfter.value = [];
      form.quantity = undefined;
      form.collectionDate = new Date().toISOString().slice(0, 10); // 
      form.collectionTime = '08:00'; // 
    };

    const goBack = () => {
      // 
      if (isAdmin.value) {
        router.push('/');
      }
    };

    const viewRecords = () => {
      router.push({ name: 'RecordsList', params: { unitId: props.id } });
    };

    const selectAllText = (event) => {
      // setTimeoutDOM
      setTimeout(() => {
        if (event && event.target) {
          // el-input-numberinput
          const inputEl = event.target.querySelector('input');
          if (inputEl) {
            inputEl.select();
          }
        }
      }, 10);
    };

    return {
      form,
      rules,
      wasteForm,
      loading,
      unitName,
      wasteTypes,
      isAdmin,
      fileListBefore,
      fileListAfter,
      handlePhotoBeforeChange,
      handlePhotoBeforeRemove,
      handlePhotoAfterChange,
      handlePhotoAfterRemove,
      handleBeforeUpload,
      submitForm,
      resetForm,
      goBack,
      viewRecords,
      selectAllText,
      showUploadProgress,
      uploadPercentage,
      uploadStatus,
      percentageFormat,
      showLargeFileWarning
    };
  }
};
</script>

<style scoped>
.waste-form-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  background-color: #f5f7fa;
}

.header {
  background-color: #409EFF;
  color: white;
  padding: 12px 16px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: sticky;
  top: 0;
  z-index: 10;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
}

.header h1 {
  font-size: 18px;
  margin: 0;
  font-weight: 500;
}

.back-button, .view-records {
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 5px;
  font-size: 14px;
  white-space: nowrap;
}

.content {
  flex: 1;
  padding: 16px;
  max-width: 100%;
  margin: 0 auto;
  width: 100%;
  box-sizing: border-box;
}

.unit-info {
  text-align: center;
  margin-bottom: 16px;
}

.unit-info h2 {
  color: #333;
  border-bottom: 2px solid #409EFF;
  display: inline-block;
  padding-bottom: 5px;
  font-size: 18px;
  margin: 0;
}

.waste-form {
  background-color: #ffffff;
  padding: 16px;
  border-radius: 8px;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
}

.form-row {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

@media (min-width: 768px) {
  .form-row {
    flex-direction: row;
  }
  
  .date-item, .time-item {
    flex: 1;
  }
  
  .content {
    padding: 24px;
    max-width: 800px;
  }
  
  .header h1 {
    font-size: 22px;
  }
}

.waste-photo-uploader {
  width: 100%;
}

.waste-photo-uploader :deep(.el-upload--picture-card) {
  width: 80px;
  height: 80px;
  line-height: 80px;
}

.waste-photo-uploader :deep(.el-upload-list--picture-card .el-upload-list__item) {
  width: 80px;
  height: 80px;
}

.photo-tip, .time-tip {
  font-size: 12px;
  color: #666;
  margin-bottom: 8px;
}

.form-actions {
  display: flex;
  justify-content: center;
  gap: 12px;
  margin-top: 24px;
}

.submit-btn, .reset-btn {
  width: 45%;
  height: 44px;
  font-size: 16px;
  border-radius: 8px;
}

@media (min-width: 768px) {
  .form-actions {
    justify-content: flex-start;
  }
  
  .submit-btn, .reset-btn {
    width: auto;
  }
}

.footer {
  background-color: #f5f5f5;
  padding: 12px;
  text-align: center;
  color: #666;
  font-size: 12px;
}

:deep(.el-form-item__label) {
  font-size: 15px;
  font-weight: 500;
  color: #333;
  padding-bottom: 4px;
}

:deep(.el-input__inner), 
:deep(.el-select__input), 
:deep(.el-input-number__decrease), 
:deep(.el-input-number__increase) {
  height: 44px;
  font-size: 15px;
}

:deep(.el-button) {
  font-size: 15px;
  padding: 12px 20px;
}

:deep(.el-select-dropdown__item) {
  font-size: 15px;
  padding: 12px 20px;
}

:deep(.el-date-editor.el-input), 
:deep(.el-date-editor.el-input__inner) {
  width: 100%;
}

:deep(.el-form-item) {
  margin-bottom: 20px;
}

:deep(.date-picker-popup),
:deep(.time-picker-popup) {
  touch-action: none;
}

:deep(.el-input__wrapper) {
  cursor: pointer;
}

:deep(.el-input__inner) {
  -webkit-tap-highlight-color: transparent;
  user-select: none;
}

.upload-warning {
  margin-bottom: 10px;
}

.upload-progress {
  text-align: center;
  padding: 10px;
}

.upload-status {
  margin-top: 10px;
  color: #606266;
  font-size: 14px;
}
</style>
