/**
 * ExcelExcelJS
 * @param {Array} data - 
 * @param {String} fileName - 
 * @param {Array} headers - 
 * @param {Object} imageData - 
 */
export const exportToExcel = async (data, fileName, headers, imageData = null) => {
  if (!data || data.length === 0) {
    console.error('');
    return false;
  }

  // 
  fileName = fileName.replace(/[\\/:*?"<>|]/g, '_');
  const timestamp = new Date().toISOString().replace(/[-:.]/g, '').substring(0, 14);
  
  console.log(':', data.length);
  console.log(':', imageData ? '' : '');
  if (imageData) {
    console.log(':', Object.keys(imageData).length);
    console.log(':', JSON.stringify(Object.keys(imageData).slice(0, 2).reduce((obj, key) => {
      obj[key] = imageData[key];
      return obj;
    }, {})));
  }
  
  try {
    console.log('ExcelJSExcel...');
    
    // 
    const workbook = new ExcelJS.Workbook();
    workbook.creator = '';
    workbook.lastModifiedBy = '';
    workbook.created = new Date();
    workbook.modified = new Date();
    
    // 
    const worksheet = workbook.addWorksheet('');
    
    // 
    const columns = headers.map(header => ({
      header: header.text,
      key: header.field,
      width: 15
    }));
    worksheet.columns = columns;
    
    // 
    data.forEach((record, rowIndex) => {
      const row = {};
      headers.forEach(header => {
        row[header.field] = record[header.field] || '';
      });
      worksheet.addRow(row);
      
      // 
      if (imageData && imageData[rowIndex]) {
        console.log(`${rowIndex+1}:`, JSON.stringify(Object.keys(imageData[rowIndex])));
        
        // 
        if (imageData[rowIndex][7] && imageData[rowIndex][7].base64) {
          try {
            // base64
            const beforePhotoBase64 = imageData[rowIndex][7].base64;
            // base64
            const base64Data = beforePhotoBase64.startsWith('data:') 
              ? beforePhotoBase64 
              : `data:image/jpeg;base64,${beforePhotoBase64}`;
              
            const beforePhotoId = workbook.addImage({
              base64: base64Data,
              extension: 'jpeg',
            });
            
            // 
            const beforePhotoColIndex = 7; // 87
            
            // 
            worksheet.addImage(beforePhotoId, {
              tl: { col: beforePhotoColIndex, row: rowIndex + 1 },
              ext: { width: 80, height: 80 }
            });
            
            // 
            worksheet.getRow(rowIndex + 2).height = 60;
            console.log(`${rowIndex+1}`);
          } catch (imgError) {
            console.error(` ${rowIndex + 1}:`, imgError);
          }
        }
        
        // 
        if (imageData[rowIndex][8] && imageData[rowIndex][8].base64) {
          try {
            // base64
            const afterPhotoBase64 = imageData[rowIndex][8].base64;
            // base64
            const base64Data = afterPhotoBase64.startsWith('data:') 
              ? afterPhotoBase64 
              : `data:image/jpeg;base64,${afterPhotoBase64}`;
              
            const afterPhotoId = workbook.addImage({
              base64: base64Data,
              extension: 'jpeg',
            });
            
            // 
            const afterPhotoColIndex = 8; // 98
            
            // 
            worksheet.addImage(afterPhotoId, {
              tl: { col: afterPhotoColIndex, row: rowIndex + 1 },
              ext: { width: 80, height: 80 }
            });
            
            // 
            worksheet.getRow(rowIndex + 2).height = 60;
            console.log(`${rowIndex+1}`);
          } catch (imgError) {
            console.error(` ${rowIndex + 1}:`, imgError);
          }
        }
      }
    });
    
    // 
    worksheet.getRow(1).font = { bold: true };
    worksheet.getRow(1).alignment = { vertical: 'middle', horizontal: 'center' };
    
    // Excel
    const excelFileName = `${fileName}_${timestamp}.xlsx`;
    console.log('Excel:', excelFileName);
    
    // buffer
    const buffer = await workbook.xlsx.writeBuffer();
    
    // Blob
    const blob = new Blob([buffer], { 
      type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' 
    });
    
    // 
    const url = URL.createObjectURL(blob);
    const link = document.createElement('a');
    link.href = url;
    link.download = excelFileName;
    
    // 
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
    URL.revokeObjectURL(url);
    
    console.log('Excel');
    return true;
  } catch (error) {
    console.error('Excel:', error);
    console.log('CSV');
    // ExcelCSV
    exportToCSV(data, fileName, headers);
    return false;
  }
};

/**
 * CSV
 * @param {Array} data - 
 * @param {String} fileName - 
 * @param {Array} headers - 
 */
export const exportToCSV = (data, fileName, headers) => {
  // 
  const timestamp = new Date().toISOString().replace(/[-:.]/g, '').substring(0, 14);
  const csvFileName = `${fileName}_${timestamp}.csv`;
  
  // CSV
  let csvContent = '\uFEFF'; // BOM
  
  // 
  const headerRow = headers.map(h => `"${h.text}"`).join(',');
  csvContent += headerRow + '\r\n';
  
  // 
  data.forEach(item => {
    const row = headers.map(header => {
      const value = item[header.field];
      
      // nullundefined
      if (value === null || value === undefined) {
        return '';
      }
      
      // 
      if (header.type === 'number' || typeof value === 'number') {
        return value;
      }
      
      // 
      let cellValue = String(value);
      if (cellValue.includes(',') || cellValue.includes('"') || cellValue.includes('\n')) {
        cellValue = cellValue.replace(/"/g, '""'); // 
        cellValue = `"${cellValue}"`; // 
      }
      
      return cellValue;
    }).join(',');
    
    csvContent += row + '\r\n';
  });
  
  // 
  const blob = new Blob([csvContent], { type: 'text/csv;charset=utf-8;' });
  const url = URL.createObjectURL(blob);
  const link = document.createElement('a');
  link.setAttribute('href', url);
  link.setAttribute('download', csvFileName);
  link.style.visibility = 'hidden';
  document.body.appendChild(link);
  link.click();
  document.body.removeChild(link);
  URL.revokeObjectURL(url);
  
  console.log('CSV');
};

/**
 * URLBase64
 * @param {string} url URL
 * @returns {Promise<string>} Base64
 */
export const imageUrlToBase64 = async (url) => {
  console.log('Base64:', url);
  
  // URL
  if (!url || typeof url !== 'string') {
    console.error('URL:', url);
    return null;
  }
  
  // 
  const urlWithCache = url.includes('?') 
    ? `${url}&_t=${new Date().getTime()}` 
    : `${url}?_t=${new Date().getTime()}`;
  
  // 1fetch API
  try {
    console.log('fetch API...');
    const response = await fetch(urlWithCache, { 
      mode: 'cors',
      cache: 'no-cache',
      credentials: 'same-origin',
      headers: {
        'Cache-Control': 'no-cache',
        'Pragma': 'no-cache'
      }
    });
    
    if (!response.ok) {
      throw new Error(`HTTP: ${response.status}`);
    }
    
    const blob = await response.blob();
    console.log(':', blob.size, ', :', blob.type);
    
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.onloadend = () => {
        const result = reader.result;
        console.log('Base64:', result.length);
        // base64
        const base64Data = result.split(',')[1];
        resolve(base64Data);
      };
      reader.onerror = (e) => {
        console.error('FileReader:', e);
        reject(e);
      };
      reader.readAsDataURL(blob);
    });
  } catch (error) {
    console.warn('fetch API:', error);
    
    // 2Image
    try {
      console.log('Image...');
      const img = new Image();
      img.crossOrigin = 'Anonymous'; // 
      
      // 
      await new Promise((resolve, reject) => {
        img.onload = resolve;
        img.onerror = (e) => {
          console.error(':', e);
          reject(e);
        };
        img.src = urlWithCache;
      });
      
      console.log(':', img.width, 'x', img.height);
      
      // canvas
      const canvas = document.createElement('canvas');
      canvas.width = img.width;
      canvas.height = img.height;
      const ctx = canvas.getContext('2d');
      ctx.drawImage(img, 0, 0);
      
      // base64
      const base64 = canvas.toDataURL('image/jpeg');
      console.log('Base64:', base64.length);
      // base64
      const base64Data = base64.split(',')[1];
      return base64Data;
    } catch (error2) {
      console.error('Image:', error2);
      return null;
    }
  }
};

/**
 * 
 * @param {Array} records 
 * @param {string} baseUrl URL
 * @returns {Promise<Object>} 
 */
export const prepareImageExportData = async (records, baseUrl) => {
  console.log(':', records.length);
  console.log('URL:', baseUrl);
  
  // 
  if (records.length > 0) {
    console.log(':');
    const firstRecord = records[0];
    Object.keys(firstRecord).forEach(key => {
      console.log(`${key}: ${typeof firstRecord[key] === 'object' ? JSON.stringify(firstRecord[key]) : firstRecord[key]}`);
    });
  }
  
  const imageData = {};
  let processedCount = 0;
  let successCount = 0;
  
  // 
  const parsePhotoPath = (path) => {
    if (!path) return [];
    
    try {
      // JSON
      if (path.startsWith('[') && path.endsWith(']')) {
        return JSON.parse(path);
      }
      // JSON
      return [path];
    } catch (error) {
      console.error(':', error);
      // 
      return [path];
    }
  };
  
  for (let i = 0; i < records.length; i++) {
    const record = records[i];
    console.log(` ${i+1}/${records.length}, ID: ${record.id}`);
    
    // 
    imageData[i] = {};
    
    // 
    if (record.photo_path_before) {
      const beforePhotoPaths = parsePhotoPath(record.photo_path_before);
      console.log(` ${i+1} :`, beforePhotoPaths);
      
      if (beforePhotoPaths.length > 0) {
        const beforePhotoUrl = `${baseUrl}${beforePhotoPaths[0]}`;
        console.log(` ${i+1} URL:`, beforePhotoUrl);
        
        try {
          const beforePhotoBase64 = await imageUrlToBase64(beforePhotoUrl);
          if (beforePhotoBase64) {
            imageData[i][7] = {
              url: beforePhotoUrl,
              base64: beforePhotoBase64
            };
            console.log(` ${i+1} `);
            successCount++;
          } else {
            console.warn(` ${i+1} `);
            // URL
            imageData[i][7] = {
              url: beforePhotoUrl
            };
          }
        } catch (error) {
          console.error(` ${i+1} :`, error);
          // URL
          imageData[i][7] = {
            url: beforePhotoUrl
          };
        }
      }
    } else {
      console.log(` ${i+1} `);
    }
    
    // 
    if (record.photo_path_after) {
      const afterPhotoPaths = parsePhotoPath(record.photo_path_after);
      console.log(` ${i+1} :`, afterPhotoPaths);
      
      if (afterPhotoPaths.length > 0) {
        const afterPhotoUrl = `${baseUrl}${afterPhotoPaths[0]}`;
        console.log(` ${i+1} URL:`, afterPhotoUrl);
        
        try {
          const afterPhotoBase64 = await imageUrlToBase64(afterPhotoUrl);
          if (afterPhotoBase64) {
            imageData[i][8] = {
              url: afterPhotoUrl,
              base64: afterPhotoBase64
            };
            console.log(` ${i+1} `);
            successCount++;
          } else {
            console.warn(` ${i+1} `);
            // URL
            imageData[i][8] = {
              url: afterPhotoUrl
            };
          }
        } catch (error) {
          console.error(` ${i+1} :`, error);
          // URL
          imageData[i][8] = {
            url: afterPhotoUrl
          };
        }
      }
    } else {
      console.log(` ${i+1} `);
    }
    
    // 
    if (record.unit_name) {
      imageData[i].unit_name = record.unit_name;
    }
    
    processedCount++;
  }
  
  console.log(`: ${processedCount}/${records.length}, : ${successCount}`);
  return imageData;
};
